.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: #fff;
  animation: anim2 1s cubic-bezier(0.77, 0, 0.175, 1) 5s both;
}

@keyframes anim2 {
  to {
    transform: translateY(-100%);
  }
}

